.showcase {
  @include for-tablet-portrait-up {
    height: 440px;
  }

  @include for-desktop-up {
    height: 394px;
  }
  height: 237px;

  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  font-family: $title-font;
  text-align: center;
  color: $basic-white;
  letter-spacing: 0;

  background-color: $dark-cian;
}

.showcase--extra-background {
  @include for-phone-only {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 462px;
      background: url("../img/hand-with-mobile--mobile.png") 50% 176px / 320px 286px no-repeat, url("../img/back-mobile.jpg") center / cover no-repeat, $dark-cian;
      z-index: 0;

      @media (min-resolution: 2dppx) {
        background: url("../img/hand-with-mobile--mobile@2x.png") 50% 176px / 320px 286px  no-repeat, url("../img/back-mobile@2x.jpg") left top / cover no-repeat, $dark-cian;
      }
    }
  }

  @include for-tablet-portrait-up {
    padding-bottom: 290px;

    background: url("../img/hand-with-mobile--tablet@1x.png") 50% bottom no-repeat, url("../img/back-tablet@1x.jpg") left top / 100% no-repeat, $dark-cian;

    @media (min-resolution: 2dppx) {
      background: url("../img/hand-with-mobile--tablet@2x.png") 50% bottom / 660px 545px no-repeat, url("../img/back-tablet@2x.jpg") left top / 100% no-repeat, $dark-cian;
    }
  }

  @include for-desktop-up {
    height: 737px;
    padding-bottom: 115px;
    z-index: 1;

    background: url("../img/hand-with-mobile--desktop@1x.png") calc(50% + 70px)  bottom no-repeat, url("../img/back-desktop@1x.jpg") left top / 100% no-repeat, $dark-cian;

    @media (min-resolution: 2dppx) {
      background: url("../img/hand-with-mobile--desktop@2x.png") calc(50% + 70px) bottom / 1050px 645px no-repeat, url("../img/back-desktop@2x.jpg") left top / 100% no-repeat, $dark-cian;
    }
  }

  padding-bottom: 495px;
  position: relative;
}

.showcase--simple-background {
  @include for-tablet-portrait-up {
    background: url("../img/back-tablet@1x.jpg") left top / 100% no-repeat, $dark-cian;

    @media (min-resolution: 2dppx) {
      background: url("../img/back-tablet@2x.jpg") left top / 100% no-repeat, $dark-cian;
    }
  }

  @include for-desktop-up {
    background: url("../img/back-desktop@1x.jpg") left top / 100% no-repeat, $dark-cian;

    @media (min-resolution: 2dppx) {
      background: url("../img/back-desktop@2x.jpg") left top / 100% no-repeat, $dark-cian;
    }
  }

  background: url("../img/back-mobile.jpg") left top / 100% 462px no-repeat, $dark-cian;
}

.showcase__title {
  @include content-position;

  @include for-tablet-portrait-up {
    font-size: 50px;
    line-height: 68px;
    padding-top: 178px;
    padding-bottom: 0;
  }

  @include for-desktop-up {
    padding-top: 169px;
  }

  padding-top: 94px;
  padding-bottom: 12px;
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
}

.showcase__content-box {
  @include content-position;

  @include for-tablet-portrait-up {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 200px;
    padding-right: 340px;

    background: transparent;
  }

  @include for-desktop-up {
    padding-top: 207px;
    padding-right: 768px;
  }

  padding-top: 89px;
}

.showcase__slogan-box {
  @include for-phone-only {
    position: relative;
    z-index: 1;
  }

  @include for-tablet-portrait-up {
    padding: 3px 20px 0;

    background: transparent;
  }

  padding-bottom: 315px;
}

.showcase__slogan {
  @include for-tablet-portrait-up {
    font-weight: 300;
    font-size: 40px;
    line-height: 48px;
  }

  @include for-desktop-up {
    font-size: 50px;
    font-weight: 300;
    line-height: 68px;
    letter-spacing: 0;
  }

  margin: 0 10px 0;
}

.showcase__refs-block {
  @include for-tablet-portrait-up {
    padding-top: 29px;
  }

  @include for-desktop-up {
    padding-top: 35px;
  }

  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 26px;
}

.showcase__default-ref {
  @include for-tablet-portrait-up {
    width: 300px;
  }

  @include for-desktop-up {
    font-size: 21px;
    font-weight: 700;
    line-height: 18px;
  }

  display: block;
  box-sizing: border-box;
  width: 280px;
  padding-top: 17px;
  padding-bottom: 15px;

  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: $basic-white;

  border: 3px solid $basic-white;
  border-radius: 29px;
}

.showcase__download-refs-box {
  @include for-tablet-portrait-up {
    margin: 47px auto 0;
  }

  @include for-desktop-up {
    margin: 69px auto 0;
  }

  display: grid;
  align-items: center;
  width: 240px;
  margin: 33px auto 0;

  grid-template-columns: repeat(3, 1fr);
}

.showcase__download-refs-description {
  @include for-tablet-portrait-up {
    font-size: 18px;
    line-height: 24px;
    margin: 27px 0 0;
  }

  @include for-desktop-up {
    margin-top: 45px;
  }

  margin: 15px 0 0;

  font-size: 14px;
  line-height: 24px;
}
